import CryptoJS from 'crypto-js'

var myKey="12342234323442345234623472348234"
var myIv="3456445654566456"
// 加密
export function encrypt(str) { 
    str=mixStr(str);
    var key = CryptoJS.enc.Utf8.parse(myKey);
    var iv = CryptoJS.enc.Utf8.parse(myIv);

    var encrypted = '';

    var srcs = CryptoJS.enc.Utf8.parse(str);
    encrypted = CryptoJS.AES.encrypt(srcs, key, {
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    });
    return encrypted.ciphertext.toString();
}

// 解密
export function decrypt (str) {
    var key = CryptoJS.enc.Utf8.parse(myKey);
    var iv = CryptoJS.enc.Utf8.parse(myIv);
    var encryptedHexStr = CryptoJS.enc.Hex.parse(str);
    var srcs = CryptoJS.enc.Base64.stringify(encryptedHexStr);
    var decrypt = CryptoJS.AES.decrypt(srcs, key, {
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    });
    var decryptedStr = decrypt.toString(CryptoJS.enc.Utf8);
    return backMixStr(decryptedStr.toString())
}
// 错位
export function mixStr(str){
    var n=[];
    for(var i=0;i<str.length;i++){
        var s=str.charAt(i);
        var code=s.charCodeAt();
        n.push(String.fromCharCode(code+1))
    }
    return n.join('')
}
// 解错位
export function backMixStr(str){
    var n=[];
    for(var i=0;i<str.length;i++){
        var s=str.charAt(i);
        var code=s.charCodeAt();
        n.push(String.fromCharCode(code-1))
    }
    return n.join('')
}


