<template>
    <div class="yingyong">
        <headerBar :title="headerNavData" @parentsanwallet="parentsanwallet"></headerBar>
        <div class="bindwirwallet">
          <input type="text" v-model="ywalletpublick" placeholder="请扫描硬件钱包地址绑定钱包" class="walletAddress">
          <!-- <yd-button size="large" type="danger" @click.native="coinB=true" v-if="showbtn">生成同步数据</yd-button> -->
          <button class="bindbtn" @click="coinB=true" v-if="showbtn">生成同步数据</button>
          <div class="Synchronousdata" v-if="qcodeshow">
            <div id="qrcode"></div>
            <p>请用硬件钱包扫描二维码</p>
          </div>
        </div>
        <!-- //请选择币种 -->
        <yd-popup v-model="coinB" position="bottom" height="30%">
            <div class="coinlist">
              <p @click="selectCoin('CNY'),coinB=false">CNY</p>
              <p @click="selectCoin('USD'),coinB=false">USD</p>
              <p @click="selectCoin('JPY'),coinB=false">JPY</p>
              <p @click="selectCoin('KRW'),coinB=false">KRW</p>
            </div>
        </yd-popup>
        <!-- 引用页面 -->
        <div class="iframBox" v-if="websrc!=''&&websrc!=null&&websrc!=undefined">
          <yd-navbar bgcolor="#2c94ed">
              <div class="iframback" slot="left" @click="closeiFrame"><img src="../assets/images/cha.png"></div>
          </yd-navbar>
          <iframe id="iframe" :src="websrc" frameborder="0" width="100%" height="100%"></iframe>
        </div>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import QRCode from 'qrcodejs2'
  import {getCookie,setCookie} from '@/assets/js/cookie'
  import { ToZhuanZhang,Setvuefghfvmdfgdfgdfg,Getvuefghfvmdfgdfgdfg, } from '@/assets/js/Keytoken'
  import {encrypt,decrypt} from '@/utils/lib'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:'硬件钱包',
         right:true,
         bj:'#2c94ed',
         code_sao:true
      },
      coinB:false,
      dataCoin:'',
      ywalletpublick:'',
      showbtn:true,
      qcodeshow:false,
      websrc:'https://www.baidu.com/'
    }
  },
  created(){
    window.ToZhuanZhang=ToZhuanZhang;
    this.ywalletpublick = getCookie("ywalletpublick")
  },
  mounted(){
    
  },
  methods:{
    //扫一扫
    parentsanwallet(){
      Setvuefghfvmdfgdfgdfg(this);
      window.QRUtil.startScanQR('3','ToZhuanZhang')
    },
    //选择币种
    selectCoin(coin){
      var ywalletpublick = this.ywalletpublick
      if(ywalletpublick==""||ywalletpublick==undefined||ywalletpublick==null){
        this.Toast("请绑定硬件钱包")
        return
      }
      this.qcodeshow = true
      this.GetDataFromService(coin)
      this.showbtn=false
    },
    //关闭iframe
    closeiFrame(){
      this.websrc = ""
    },
    // 查询币种个数
    GetDataFromService(coin){
       this.axios({
          url:this.Url + 'ShowCurrency.ashx',
          method:'post',
          headers:{'Content-Type':'application/x-www-form-urlencoded'},
          params: {req:4,publickey:this.ywalletpublick,moneycode: coin}
        }).then(res=>{
          var data = [];
          var list = res.data.data
          var obj="";
          for(var i=0;i<list.length;i++){
             obj+=list[i].Amount+","+list[i].Balance+","+list[i].currName
            if(i<list.length - 1){
              obj += ";";
            }
          }
          var qcode = JSON.stringify(data)
          //console.log(encrypt(qcode)) 
          var a = encrypt(qcode)
          //console.log(decrypt(a))
          new QRCode('qrcode', {  
              width: 200,  
              height: 200, 
              text: obj,
              correctLevel : QRCode.CorrectLevel.L
          }) 
        })
    }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.bindwirwallet{
  padding: .3rem;
}
.walletAddress{
  font-size: .28rem;
  width: 100%;
  padding:0 10px;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  border: none;
  border-bottom: 1px solid rgb(44, 148, 237);
}
.wallet_code_sao{
  width: .44rem;
}
#qrcode{
  width: 200px;
  height: 200px;
  margin: 1.5rem auto .35rem
}
.coinlist{
  padding: 0 .3rem;
}
.coinlist p{
  border-bottom: 1px solid #f4f4f4;
  text-align: center;
  font-size: .3rem;
  padding: .28rem 0;
}
.coinlist p:last-child{
  border-bottom:none
}
.bindbtn{
  width: 90%;
  height: .8rem;
  line-height: .8rem;
  text-align: center;
  background: rgb(44, 148, 237);
  color: #fff;
  border-radius: 5px;
  border: none;
  outline: none;
  display: block;
  margin: 2rem auto 0;
  font-size: .3rem;
  box-shadow: 0 2px 3px #eee;
}
.Synchronousdata p{
  text-align: center;
  margin-top: .3rem;
  font-size: .28rem;
  color: #888
}
.iframBox{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 9999;
}
.iframback img{
  width: .35rem;
  display: block
}
</style>



